@import url('https://fonts.googleapis.com/css2?family=Poppins:wght@300&display=swap');
*{
  margin : 0;
  box-sizing: border-box;
  font-family: 'Poppins', sans-serif;}

body{
  color : #e1ebfc;
  background-color: #070804;
}

@import url('https://fonts.googleapis.com/css2?family=Cinzel:wght@500&display=swap');
.bar{
    position: fixed;
    z-index: 10001;
    width: 100%;
    top: 0;
    padding: 30px 40px 10px;
}
.nav, .nav a{
    display: flex;
    justify-content: space-between;
    align-items: center;
}
.nav a{
    text-decoration: none;
    font-size: 25px;
    color: #d9d3d3;
    font-family: 'Cinzel', serif;
}
.nav a img{
    margin: 2px 7px;
    height: 28px;
}

.bt{
    background: transparent;
    color: #d9d3d3;
    border: 2px solid #d9d3d3;
    margin: 2px 10px;
    padding: 7px;
    width: 100px;
    border-radius: 5px;
    transition: 0.4s;
}

.bt.active, .bt:hover{
    background-color: #d9d3d3;
    color: black;
}

.head{
    display: flex;
    justify-content: space-between;
    align-items: center;
    width: 100%;
    padding: 10px 60px 10px 20px;
    position: fixed;
    z-index: 10001;
    background : #070804;
}

.head .search{
    position: relative;
    border: 2px solid #d9d3d3;
    color: #d9d3d3;
    margin-right: 7px;
    padding: 1px 100px 0 10px;
    border-radius: 1px;
}
.search button{
    position: absolute;
    top: 0;
    right: 0;
    background-color: #d9d3d3;
    border: none;
}

.search button:hover{
    background-color: white;
}

.search i, .close i{
    font-size: 25px;
}

.verses{
    background-color: #d9d3d3;
    color: black;
    margin: 10px;
    padding: 20px;
    display: flex;
    border-radius: 8px;
}

.verses span{
    margin-right: 6px;
}

.verses:hover{
    transform: scale(1.009);
}

.verse_tab{
    min-height: 100vh;
    position: relative;
    top: calc(70px + 80px);
    margin: 0 20px;
    overflow: hidden;
}
.grpbtn .bt{
    margin: 0;
    border: 1px solid #d9d3d3;
    border-radius: 0;
}
.search_book .head{
    top: calc(82px + 60px)
}
.search_tab{
    position: relative;
    top: 55px;
}
.book_tab{
    margin: 20px auto;
    margin-bottom: 100px;
    display: flex;
    flex-wrap: wrap;
    justify-content: center;
}

.card{
    display: flex;
    align-items: center;
    justify-content: center;
    width: 10rem;
    height: 5rem;
    margin: 10px;
    background-color: #d9d3d3;
    color: black;
    border-radius: 7px;
}
.card h2, .card h3{
  pointer-events: none;
}


.card.active, .card:hover{
    background : linear-gradient(rgba(0, 0, 0, 0.5), rgba(0, 0, 0, 0.5)), white;
  
}

.book_card .hover.active{
    background : linear-gradient(rgba(0, 0, 0, 0.5), rgba(0, 0, 0, 0.5));
}

.book_card .innercard{
    width: 345px;
    height: 200px;
}

.card:hover, .book_card:hover{
    transform: scale(1.03);
}
.version_window{
    position: fixed;
    display: flex;
    justify-content: center;
    align-items: center;
    top: 0;
    bottom: 0;
    width: 100%;
    z-index: 100003;
    background-color: rgba(0, 0, 0, 0.3);
}
.version_window .modal{
    margin-right: 40px;
    background-color: #d9d3d3;
    color: #070804;
    padding: 20px;
    border-radius: 7px;
}
.verse_tab .modal .mbtn{
    width: 100%;
    padding: 10px;
    border: 2px solid black;
    margin: 0 0 5px 0;
    background: none;
    border-radius: 7px;
}
.verse_tab .modal .mbtn:hover, .verse_tab .modal .mbtn.active{
    background-color: rgba(1, 1, 1, 0.505);
}
.version_window .modal i:hover{
    color: aliceblue;
}

@media screen and (max-width:760px) {
    .nav a{
        font-size: 15px;
    }
    .bar{
        padding-bottom: 23px;
    }
    .nav a img{
        height: 18px;
    }
    .card{
        width: 50px;
        height: 50px;
    }
    .search_book .head{
        top : calc(82px + 40px)
    }
    .book_card .innercard{
        width: 140px;
        height: 100px;
    }
    .search_tab{
        top: 35px;
    }
    .verses{
        padding: 10px;
    }
    .verse_tab{
        top: 130px;
    }
    .verses p, .verses span{
        font-size: 13px;
    }
    .book_card:hover{
        transform: scale(1.02);
    }
    .head .search{
        width: 195px;
        padding-right: 40px;
    }
    .bt{
        width: 65px;
        padding: 3px;
        margin: 2px;
        font-size: 12px;
    }
    .nav .bt{
        width: 60px;
    }

    h2, .head i{
        font-size: 13px;
    }

    .version_window .modal{
        width: 100%;
        margin-right: 40px;
    }
    .version_window .modal h1{
        font-size: 25px
    }
}